@media (width <= 320px) {
  .about__title {
    padding: 136px 16px 0;
    font-size: 38px;
  }
}

.hero-grid {
  background-color: #121212;
  grid-template-rows: repeat(5, auto);
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
  padding: 0 40px 96px 155px;
  transition: all .3s;
  display: grid;
}

@media (width >= 769px) and (width <= 1024px) {
  .hero-grid {
    padding: 0 40px 96px;
  }
}

@media (width <= 768px) {
  .hero-grid {
    padding: 0 40px 96px;
  }
}

.hero-grid__heading {
  color: #fff;
  grid-area: 1 / 3 / auto / 5;
  font-size: clamp(24px, 2.5vw, 28px);
  line-height: 1.4;
  transition: font-size .3s;
}

@media (width <= 426px) {
  .hero-grid__heading {
    text-align: center;
  }
}

.hero-grid__word {
  color: #222324;
  margin: 0;
  font-size: clamp(28px, 8vw, 64px);
  font-weight: bold;
  line-height: 1;
  transition: font-size .3s;
}

.hero-grid__word--flexible {
  grid-area: 2 / 1 / auto / 3;
}

.hero-grid__word--professional {
  grid-area: 3 / 2 / auto / 5;
}

.hero-grid__word--local {
  grid-area: 4 / 1 / auto / 4;
}

.hero-grid__description {
  color: #adb5bd;
  grid-area: 2 / 3 / auto / 5;
  font-size: clamp(12px, 2vw, 18px);
  line-height: 1.6;
  transition: font-size .3s;
}

.hero-grid__booking, .hero-grid__trusted, .hero-grid__find-top {
  color: #adb5bd;
  font-size: clamp(12px, 1.8vw, 16px);
  line-height: 1.6;
  transition: font-size .3s;
}

.hero-grid__booking {
  grid-area: 3 / 1 / auto / 2;
}

.hero-grid__trusted {
  grid-area: 4 / 4 / auto / 5;
}

.hero-grid__find-top {
  grid-area: 5 / 3 / auto / 4;
}

@media (width <= 426px) {
  .hero-grid {
    flex-direction: column;
    row-gap: 16px;
    padding: 0 16px 48px;
